 }  // overflow: hidden


.App {
  width: 100vw;
  height: 100vh;
  background: linear-gradient(90deg, rgba(0,221,255,1) 0%, rgba(255,0,204,1) 100%);
 }  // overflow-y: scroll


.Viewer {
  width: 100%;
  height: 100%;
  // @extend .noselect
  outline: none;
  * {
    @extend .noselect;
    outline: none; } }


.start {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 2rem;
  font-size: 2rem; }
